import {Grid, Typography} from "@pankod/refine-antd";
import {useNavigation} from "@pankod/refine-core";
import {Card, Space, Col, Row, Skeleton} from "@pankod/refine-antd";
import {gqlDataProvider} from "api";
import {UserAnalytiData} from "apps/core/interfaces";
import {useEffect, useState} from "react";
const {Title, Text} = Typography;

interface Props {
  randKey?: any;
}

export const UserAnalytics: React.FC<Props> = (props: Props) => {
  const [analytics, setAnalytics] = useState<UserAnalytiData>();
  const [messageBalance, setMessageBalance] = useState<number>(0);
  const [loading, setLoading] = useState(true);
  const {push} = useNavigation();

  const breakpoint = Grid.useBreakpoint();
  const isMobile = !breakpoint.lg;

  const getUserAnalytics = async () => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "get",
      metaData: {
        operation: "userAnalytics",
        fields: ["analytics"],
      },
    });
    setAnalytics(data.analytics);
    setLoading(false);
  };

  const getNjiwaOpesMessageBalance = async () => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "get",
      metaData: {
        operation: "globalOpesSmsBalance",
        fields: ["data"],
      },
    });
    console.log("opes");
    console.log(data);
    if (data) {
      setMessageBalance(parseInt(data.data.balance));
    }
  };

  useEffect(() => {
    getUserAnalytics();
    getNjiwaOpesMessageBalance();
  }, []);

  return (
    <>
      <Row align="top">
        <Col span={24}>
          <Title level={5}>Users/Members/School Analytics</Title>
        </Col>
        <Col span={isMobile ? 12 : 4}>
          <Skeleton loading={loading} active>
            <Card
              hoverable={true}
              style={{marginRight: 10}}
              onClick={() => push("/control/schools")}
            >
              <Space
                direction="vertical"
                align="center"
                style={{width: "100%", justifyContent: "center"}}
              >
                <Title level={2}>{analytics?.total_schools}</Title>
                <Text>Scbools</Text>
              </Space>
            </Card>
          </Skeleton>
        </Col>

        <Col span={isMobile ? 12 : 4}>
          <Skeleton loading={loading} active>
            <Card
              hoverable={true}
              style={{marginRight: 10}}
              onClick={() => push("/control/users?status=1")}
            >
              <Space
                direction="vertical"
                align="center"
                style={{width: "100%", justifyContent: "center"}}
              >
                <Title level={2}>{analytics?.total_active_users}</Title>
                <Text>Active Users</Text>
              </Space>
            </Card>
          </Skeleton>
        </Col>

        <Col span={isMobile ? 12 : 4}>
          <Skeleton loading={loading} active>
            <Card
              hoverable={true}
              style={{marginRight: 10}}
              onClick={() => push("/control/users?status=0")}
            >
              <Space
                direction="vertical"
                align="center"
                style={{width: "100%", justifyContent: "center"}}
              >
                <Title level={2}>{analytics?.total_blocked_users}</Title>
                <Text>Blocked Users</Text>
              </Space>
            </Card>
          </Skeleton>
        </Col>

        <Col span={isMobile ? 12 : 2}>
          <Skeleton loading={loading} active>
            <Card
              hoverable={true}
              style={{marginRight: 10}}
              onClick={() => push("/control/users?status=1&role=adminstrator")}
            >
              <Space
                direction="vertical"
                align="center"
                style={{width: "100%", justifyContent: "center"}}
              >
                <Title level={2}>{analytics?.total_adminstrators}</Title>
                <Text>Active Admins</Text>
              </Space>
            </Card>
          </Skeleton>
        </Col>

        <Col span={isMobile ? 12 : 2}>
          <Skeleton loading={loading} active>
            <Card
              hoverable={true}
              style={{marginRight: 10}}
              onClick={() => push("/control/users?status=0&role=adminstrator")}
            >
              <Space
                direction="vertical"
                align="center"
                style={{width: "100%", justifyContent: "center"}}
              >
                <Title level={2}>
                  {analytics?.total_blocked_adminstrators}
                </Title>
                <Text>Blocked Admins</Text>
              </Space>
            </Card>
          </Skeleton>
        </Col>

        <Col span={isMobile ? 12 : 2}>
          <Skeleton loading={loading} active>
            <Card
              hoverable={true}
              style={{marginRight: 10}}
              onClick={() => push("/control/users?status=1&role=staff")}
            >
              <Space
                direction="vertical"
                align="center"
                style={{width: "100%", justifyContent: "center"}}
              >
                <Title level={2}>{analytics?.total_staffs}</Title>
                <Text>Active Staffs</Text>
              </Space>
            </Card>
          </Skeleton>
        </Col>

        <Col span={isMobile ? 12 : 2}>
          <Skeleton loading={loading} active>
            <Card
              hoverable={true}
              style={{marginRight: 10}}
              onClick={() => push("/control/users?status=0&role=staff")}
            >
              <Space
                direction="vertical"
                align="center"
                style={{width: "100%", justifyContent: "center"}}
              >
                <Title level={2}>{analytics?.total_blocked_staffs}</Title>
                <Text>Blocked Staffs</Text>
              </Space>
            </Card>
          </Skeleton>
        </Col>

        <Col span={isMobile ? 12 : 2}>
          <Skeleton loading={loading} active>
            <Card hoverable={true} style={{marginRight: 10}}>
              <Space
                direction="vertical"
                align="center"
                style={{width: "100%", justifyContent: "center"}}
              >
                <Title level={2}>{messageBalance}</Title>
                <Text>Messages Balance</Text>
              </Space>
            </Card>
          </Skeleton>
        </Col>
      </Row>
    </>
  );
};
