import {Breadcrumb} from "antd";
import {Link} from "react-router-dom";
import {Analytics} from "../components/analytics";
import {ControlLayout} from "../control_layout";
import {useEffect} from "react";
import urlAppendString from "services/url_append_string";
import {ControlPermitted} from "../components/permissions/control_permission";
import {useGetIdentity} from "@pankod/refine-core";

const Applications: React.FC = () => {
  return (
    <div style={{marginTop: 10}}>
      <Breadcrumb>
        <Breadcrumb.Item>
          <Link to={"/control/analytics"}>Home</Link>
        </Breadcrumb.Item>
        <Breadcrumb.Item>Analytics</Breadcrumb.Item>
      </Breadcrumb>
      <Analytics />
    </div>
  );
};

export const ControlAnalyticsPage = () => {
  const {data: user} = useGetIdentity();

  const checkIfUserIsAdmin = () => {
    if (user?.isStaff === false || user?.isAdmin === false) {
      window.location.pathname = "/home";
    }
  };

  useEffect(() => {
    checkIfUserIsAdmin();
    window.history.pushState({}, "", urlAppendString());
  }, []);
  return (
    <ControlLayout>
      <ControlPermitted>
        <Applications />
      </ControlPermitted>
    </ControlLayout>
  );
};
